<template>
	<el-container>
		<el-header style="background-color: #3BAFDA;">
			<el-row>
				<el-col :span="22">
					<div class="grid-content bg-purple">
						<router-link to="/">
							<img src="../../../img/slideShow/logo.png" />
						</router-link>
						<b>学生管理系统</b>
					</div>
				</el-col>
				<el-col :span="2">
					<div class="grid-content bg-purple-light">
						<img src="../../../img/slideShow/default.png" />
						<el-dropdown>
							<span class="el-dropdown-link" id="abb">
   							 {{names}}
    					<i class="el-icon-arrow-down el-icon--right"></i>
 						 </span>
							<el-dropdown-menu slot="dropdown">
								<el-dropdown-item>
									<router-link to="/geren">个人信息</router-link>
								</el-dropdown-item>
								<el-dropdown-item>
									<router-link to="/xiugai">修改密码</router-link>
								</el-dropdown-item>
								<el-dropdown-item divided @click.native="loginout">退出登录</el-dropdown-item>
							</el-dropdown-menu>
						</el-dropdown>
					</div>
				</el-col>
			</el-row>
		</el-header>
		<el-container>
			<el-aside>
				<router-link to="/quangbu">
					<div>
						<p class="el-icon-menu">全部成员 </p>
					</div>
				</router-link>
				</br>
				<router-link to="/geren">
					<div>
						<p class="el-icon-menu">个人信息</p>
					</div>
				</router-link>
				</br>
				<router-link to="/xiugai">
					<div>
						<p class="el-icon-menu">修改密码</p>
					</div>
				</router-link>
			</el-aside>
			<el-main>
				<router-view></router-view>
				<!--<div id="guan"v-show="isshow"><strong>欢迎来到学生管理系统</strong></div>-->
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'guanli',
		data() {
			return {
				names: null,
				isshow:true
			}

		},
		created() {
			this.names = sessionStorage.getItem("users");
			if(this.names == null) {
				this.$router.push("/")
			}
		},
		updated:function(){
			this.names=sessionStorage.getItem("users")
		},
		methods: {			
			loginout() {
				this.$confirm('确认退出吗, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$message({
						type: 'success',
						message: '删除成功!'
					});
					var that = this;
					//路由跳出到登录首页面
					that.$router.push("/")
					sessionStorage.clear()
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
		}
	}
</script>

<style scoped>
	#guan {
		text-align: center;
	}
	
	a {
		text-decoration: none;
		color: #000000;
	}
	
	.bg-purple img {
		width: 45px;
		height: 45px;
	}
	
	.bg-purple-light img {
		width: 30px;
		height: 30px;
	}
	
	.el-aside {
		padding-left: 40px;
		padding-right: 30px;
	}
	
	.el-main {
		/*text-align: center;*/
		background-color: #E9EEF3;
	}
	
	.el-aside {
		/*background-color: #35495E;*/
		height: 600px;
		background-color: #D8D8D8;
		width: 150px !important;
	}
	
	.el-header {
		/*background-color: #3BAFDA;*/
		line-height: 60px;
		border-bottom: 1px solid #fff;
	}
	
	.el-header img {
		vertical-align: middle;
		display: inline-block;
		margin-right: 10px;
	}
	/*#main{
  	background-color: #3BAFDA;
  	padding: 150px 0;
  }*/
	
	form {
		/*	width: 400px;*/
		text-align: center;
		margin: 0px auto;
		background-color: #fff;
		padding: 40px 20px 10px;
		border-radius: 5px;
		box-shadow: 2px 2px 1px 0px;
	}
	
	form button {
		/*	width: 400px;*/
	}
</style>


