<template>
	<el-container>
		<el-header>
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item><router-link to="/zhuyemian">首页</router-link></el-breadcrumb-item>
				<el-breadcrumb-item>个人信息</el-breadcrumb-item>
			</el-breadcrumb>
		</el-header>
		<el-main>
			<div >
				<el-input v-model="input1" :disabled="true">
					<template slot="prepend">编号</template>
				</el-input>
			</div>
			<div >

				<el-input v-model="input2" :disabled="true">
					<template slot="prepend">账户</template>
				</el-input>
			</div>
			<div >

				<el-input v-model="input3" :disabled="true">
					<template slot="prepend">密码</template>
				</el-input>
			</div>
		</el-main>
	</el-container>
</template>

<script>
	import axios from 'axios'
	export default {
		data() {
			return {
				input1: '',
				input2: '',
				input3: ''
			}
		},
		created(){
			this.names=sessionStorage.getItem("users");
			if(this.names==null){
				this.$router.push("/")
			}
		},
		mounted: function() {
			var that = this;
			axios.get("http://127.0.0.1:8082/getUsers")
				.then((res) => {
//					console.log(res.data);
					for(var i = 0; i < res.data.length; i++) {
						if(res.data[i].uname == sessionStorage.getItem('users')) {
							that.input1 = res.data[i].id
							that.input2 = res.data[i].uname
							that.input3 = res.data[i].pwd
						}
					}

				})
		}
	}
</script>

<style scoped>
	
	
	div{
		margin-top: 15px;
	}
	/*.el-input {
		float: left;
		width: 1300px;
	}
	
	span {
		float: left;
	}
	
	.el-row {
		margin: 15px 0px;
	}*/
</style>


