<template>
	<el-container>
		<el-header>
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item>
					<router-link to="/zhuyemian">首页</router-link>
				</el-breadcrumb-item>
				<el-breadcrumb-item>全部成员</el-breadcrumb-item>
			</el-breadcrumb>
		</el-header>
		<el-main>
			<el-table :data="tableData" style="width: 100%">
				<el-table-column prop="id" label="编号" width="250" type="index">
				</el-table-column>
				<el-table-column prop="uname" label="姓名" sortable width="360">
				</el-table-column>
				<el-table-column prop="pwd" label="密码" sortable width="360">
				</el-table-column>
				<el-table-column prop="address" label="操作" width="180">
					<template slot-scope="scope">
						<el-button @click="handleEditEnv(scope.$index, scope.row)">编辑</el-button>
						<el-dialog width="500px" title="修改成员" :visible.sync="dialogFormVisible2">
							<el-form :model="form">
								<el-form-item label="用户名" :label-width="formLabelWidth">
									<el-input v-model="form.newname" autocomplete="off"></el-input>
								</el-form-item>
								<el-form-item label="密码" :label-width="formLabelWidth">
									<el-input v-model="form.newpwd" autocomplete="off"></el-input>
								</el-form-item>
							</el-form>
							<div slot="footer" class="dialog-footer">
								<el-button @click="dialogFormVisible2 = false">取 消</el-button>
								<el-button type="primary" @click="change">确 定</el-button>
							</div>
						</el-dialog>
						<el-button @click.native.prevent="deleteRow(scope.$index, scope.row)" type="danger" size="small">
							删除
						</el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-main>
		<div class="btnDiv">
			<el-button type="primary" icon="el-icon-circle-plus-outline" @click="dialogFormVisible = true" class="po">新增</el-button>
			<el-dialog width="500px" title="新增成员" :visible.sync="dialogFormVisible">
				<el-form :model="form">
					<el-form-item label="用户名" :label-width="formLabelWidth">
						<el-input v-model="form.name" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="密码" :label-width="formLabelWidth">
						<el-input v-model="form.pwd" autocomplete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="dialogFormVisible = false">取 消</el-button>
					<el-button type="primary" @click="add">确 定</el-button>
				</div>
			</el-dialog>
		</div>
	</el-container>
</template>

<script>
	import axios from 'axios'
	export default {
		data() {
			return {
				tableData: [],
				dialogFormVisible: false,
				dialogFormVisible2: false,
				form: {
					name: '',
					pwd: '',
					newname: '',
					newpwd: '',
					id: ''
				},
				formLabelWidth: '100px',
				id: "",
				uname: '',
				pwd: ''
			};
		},
		created() {
			this.names = sessionStorage.getItem("users");
			if(this.names == null) {
				this.$router.push("/")
			}
		},

		beforeMount: function() {
			var that = this;
			axios.get('http://127.0.0.1:8082/')
				.then((res) => {
					that.tableData = res.data;
				}).catch(function(error) {})
		},
		methods: {
			huoqu() {
				axios.get('http://127.0.0.1:8082/')
					.then((res) => {
						this.tableData = res.data;
					}).catch(function(error) {})
			},
			//			编辑
			handleEditEnv(index, row) {
				console.log(index, row.uname, row.pwd);
				this.form.newname = row.uname;
				this.form.newpwd = row.pwd;
				this.form.id = row.id;
				this.dialogFormVisible2 = true;
			},
			change() {
				this.$confirm('此操作将修改账号, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					if(this.form.newname == "" || this.form.newpwd == "") {
						this.$message.error('信息不能为空');
					} else {
						if(this.form.id == sessionStorage.getItem("id")) {
							var that = this;
							axios.get('http://127.0.0.1:8082/handleEditset?id=' + that.form.id + '&pwd=' + that.form.newpwd + '&name=' + that.form.newname + '')
								.then((res) => {
									if(res.data == 'ok') {
										that.$message({
											message: '更改成功',
											type: 'success'
										});
										sessionStorage.setItem('users', that.form.newname);
										this.dialogFormVisible2 = false;

										that.huoqu()
//										this.$router.push("/guanli")
//										this.$router.push("/quangbu")
									} else {
										that.$message.error('更改失败');

									}
								})
						} else {
							var that = this;
							axios.get('http://127.0.0.1:8082/handleEditset?id=' + that.form.id + '&pwd=' + that.form.newpwd + '&name=' + that.form.newname + '')
								.then((res) => {
									if(res.data == 'ok') {
										that.huoqu()
										that.$message({
											message: '更改成功',
											type: 'success'
										});
										this.dialogFormVisible2 = false;
									} else {
										that.$message.error('更改失败');

									}
								})
						}
					}
				})
				//				this.$router.push("/quangbu")
			},
			//			删除
			deleteRow(index, row) {
				this.$confirm('此操作将删除账号, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					console.log(row)
					var that = this;
					axios.get('http://127.0.0.1:8082/handleDel?id=' + row.id + '')
						.then((res) => {
							console.log(res.data)
							if(res.data == 'ok') {
								if(row.uname == sessionStorage.getItem("users")) {
									sessionStorage.clear();
									that.$router.push("/zhuce");
									this.$message({
										type: 'success',
										message: '删除成功,请从新注册登陆!'
									});
									
								} else {
									this.$message({
										type: 'success',
										message: '删除成功!',
									});
								}
								that.huoqu();
							} else {
								that.$message.error('删除失败');
							}
						})
				})
			},
			//			增加
			add() {
				this.$confirm('此操作将添加账号, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					if(this.form.name == "" || this.form.pwd == "") {
						this.$message.error('信息不能为空');
					} else {
						var that = this;
						axios.get('http://127.0.0.1:8082/handleAddset?name=' + that.form.name + '&pwd=' + that.form.pwd + '')
							.then((res) => {
								console.log(res.data)
								if(res.data == "ok") {
									//路由跳转								
									this.$message({
										type: 'success',
										message: '添加成功!'
									});
								} else {
									that.$message.error('账号已存在，添加失败');
								}
								that.dialogFormVisible = false;
								//								that.$router.go(0)
								that.$router.push("/guanli")
								that.$router.push("/quangbu")
								that.huoqu()
							})
					}
				}).catch(() => {
					that.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			}
		}
	}
</script>

<style scoped>
	.el-breadcrumb {
		margin-top: 15px;
	}
	
	.po {
		margin-left: 570px;
	}
	
	.cell {
		padding-left: 40px;
	}
</style>


