import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import denglu from '@/components/denglu'
import zhuce from '@/components/zhuce'
import guanli from '@/components/guanli'
import quangbu from '@/components/quangbu'
import xiugai from '@/components/xiugai'
import geren from '@/components/geren'
import zhuyemian from '@/components/zhuyemian'
import nfile from '@/components/nfile'
Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			name: 'denglu',
			component: denglu
		},
		{
			path: '/zhuce',
			name: 'zhuce',
			component: zhuce
		},
//		{
//			path: '/nfile',
//			name: 'nfile',
//			component: nfile
//		},
		{
			path: '/guanli',
			component: guanli,
			redirect:"/zhuyemian",
			children: [{
					path: '/quangbu',
					name: 'quangbu',
					component: quangbu
				}, {
					path: '/xiugai',
					name: 'xiugai',
					component: xiugai

				}, {
					path: '/geren',
					name: 'geren',
					component: geren

				},
				 {
					path: '/zhuyemian',
					name: 'zhuyemian',
					component: zhuyemian

				},
				//			{
				//				path: '/bai',
				//				name: 'bai',
				//				component: bai
				//				
				//			}
			]
		}
		//  {
		//    path: '/',
		//    name: 'HelloWorld',
		//    component: HelloWorld
		//  }
	]
})


