<template>
	<div class="app">
		<div class="app-head">
			<el-header><img src="../../../img/slideShow/logo.png" style="vertical-align:middle;width: 43px;" /><strong>学生管理系统</strong></el-header>
		</div>
		<div id="abb">
			<el-container>
				<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
					<el-form-item label="账号" prop="zhanghao">
						<el-input type="text" v-model="ruleForm2.zhanghao" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="密码" prop="mima">
						<el-input type="password" v-model="ruleForm2.mima" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('ruleForm2')">注册</el-button>
					</el-form-item>
				</el-form>
			</el-container>
		</div>
	</div>

</template>

<script>
	import axios from 'axios'
	export default {

		name: 'zhuce',
		data() {
			var validatePass = (rule, value, callbacks) => {
				if(value === '') {//判断用户名是否为空
					callbacks(new Error('请输入账号'));
				} else {
					localStorage.setItem("pass", value);
					callbacks();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if(value === '') {//判断密码是否为空
					callback(new Error('请输入密码'));
				} else {
					localStorage.setItem("pwd", value);
					callback();
				}
			};
			return {
				ruleForm2: {
					zhanghao: '',
					mima: ''
				},
				rules2: {
					zhanghao: [{
						required: true,
						validator: validatePass,
						trigger: 'blur',

					}],
					mima: [{
						required: true,
						validator: validatePass2,
						trigger: 'blur',
					}]
				}
			};
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {					
					if(valid) {
						var that = this;
						axios.get('http://127.0.0.1:8082/register?name=' + that.ruleForm2.zhanghao + '&pwd=' + that.ruleForm2.mima + '')
							.then((res) => {
								console.log(res.data)
								if(res.data == "ok") {//判断返回值是否为ok
									//路由跳转
									that.$message({
										message: '注册成功',
										type: 'success'
									});
									that.$router.push("/")
								}else{
							 this.$message.error('用户已存在，注册失败');
						}
							})
					} else {
						this.$message.error('注册失败');
						return false;
					}
				});
			}

		
		//注册

	}

	}
</script>

<style scoped>
	#abb {
		margin-left: 600px;
		margin-top: 200px;
		width: 300px;
		border: 1px solid red;
		padding: 25px;
		height: 150px;
		margin: 0 auto;
		margin-top: 220px;
	}
	
	.app {
		height: 1000px;
	}
	
	.app-head {
		background: #00EEEE;
		height: 50px;
	}
</style>


