<template>
	<el-container>
  <el-header id="header">
  	<img src="../../../img/slideShow/logo.png" width="30px" height="30px"/>
  	<b>学生管理系统</b>
  </el-header>
  <el-main id="main">
  	<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" input-width="300px" class="demo-ruleForm">
  <el-form-item  prop="user" >
    <el-input type="text" v-model="ruleForm2.user" autocomplete="off" placeholder="请输入用户名"></el-input>
  </el-form-item>
  <el-form-item  prop="pwd" >
    <el-input type="password" v-model="ruleForm2.pwd" autocomplete="off" placeholder="请输入密码"></el-input>
  </el-form-item>
  <el-form-item>
  	<el-button type="primary" @click="submitForm">登录</el-button>
  </el-form-item>
  <el-form-item>
  	<el-button type="primary" @click="submitForms">注册</el-button>
  </el-form-item>
</el-form>
  </el-main>
</el-container>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'login',
		data() {
			var validateUser = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请输入用户名'));
				} else {
					
					callback();
				}
			};
			var validatePwd = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请输入密码'));
				} else {
					sessionStorage.setItem("pwd",value);
					callback();
				}
			};
			return {
				ruleForm2: {
					user: '',
					pwd: '',
					
				},
				rules2: {
					user: [{
						validator: validateUser,
						trigger: 'blur'
					}],
					pwd: [{
						validator: validatePwd,
						trigger: 'blur'
					}],
				}
			};
		},
		methods: {
			//	登录
			submitForm() {
				var that = this;
				axios.post('http://127.0.0.1:8082/login?name=' + that.ruleForm2.user + '&pwd=' + that.ruleForm2.pwd + '')
					.then((res) => {
						//					提示成功
//					var loginuser=sessionStorage.getItem("user");
//					var loginpwd=sessionStorage.getItem("pwd");
//					var zhuceuser=sessionStorage.getItem("pass");
//					var zhucepwd=sessionStorage.getItem("pwd");
//					loginuser==zhuceuser && loginpwd==zhucepwd
//					console.log(res.data.length)
//					console.log(res.data[0].id)
					if(res.data.length>0){										
							//路由跳转
							that.$message({
							message: '登录成功',
							type: 'success',
						});						
						sessionStorage.setItem("users", that.ruleForm2.user);
						sessionStorage.setItem("id", res.data[0].id);
						that.$router.push("/guanli")
						}else{
							 this.$message.error('用户名不存在');
						}
					})
			},
			submitForms() {
				var that = this;
				that.$router.push("/zhuce")
			}
		}
	}
</script>

<style scoped>
	 #header{
  	background-color: #3BAFDA;
  	line-height: 60px;
  	border-bottom:1px solid #fff;
  }
  #header img{
  	vertical-align: middle;
  	display: inline-block;
  	margin-right: 10px;
  }
  #main{
  	background-color: #3BAFDA;
  	padding: 150px 0;
  }
  form{
  	width: 400px;
  	text-align: center;
  	margin: 0px auto;
  	background-color: #fff;
  	padding: 40px 20px 10px;
  	border-radius: 5px;
  	box-shadow: 2px 2px 1px 0px;
  }
  form button {
		width: 400px;
	}
</style> 


