<template>
	<el-container>
		<el-header>
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item><router-link to="/zhuyemian">首页</router-link></el-breadcrumb-item>
				<el-breadcrumb-item>修改密码</el-breadcrumb-item>
			</el-breadcrumb>
		</el-header>
		<el-main>
			<div>
				<el-input v-model="input1" :disabled="true">
					<template slot="prepend">编号</template>
				</el-input>
			</div>
			<div>

				<el-input v-model="input2" :disabled="true">
					<template slot="prepend">账户</template>
				</el-input>
			</div>
			<div>

				<el-input v-model="input3">
					<template slot="prepend">密码</template>
				</el-input>
			</div>
			
		</el-main>
		<el-row>
				 <el-button type="primary"@click="xiugaimima">修改</el-button>
			</el-row>
	</el-container>
</template>

<script>
	import axios from 'axios'
	export default {
		data() {
			return {
				input1: '',
				input2: '',
				input3: ''
			}
		},
		created() {
			this.names = sessionStorage.getItem("users");
			if(this.names == null) {
				this.$router.push("/")
			}
		},
		mounted: function() {
			var that = this;
			axios.get("http://127.0.0.1:8082/getUsers")
				.then((res) => {
					console.log(res);
					for(var i = 0; i < res.data.length; i++) {
						if(res.data[i].uname == sessionStorage.getItem('users')) {
							that.input1 = res.data[i].id
							that.input2 = res.data[i].uname
							that.input3 = res.data[i].pwd
						}
					}

				})
		},
		methods: {
			xiugaimima() {
				var that = this;
				axios.get("http://127.0.0.1:8082/getUsers")
					.then((res) => {
					
						for(var i = 0; i < res.data.length; i++) {
							if(res.data[i].uname == sessionStorage.getItem('users')) {
								if(that.input3 == res.data[i].pwd) {
									this.$message.error('修改失败');
								} else {
									console.log(res);
									axios.get('http://127.0.0.1:8082/handleEditpws?pwd=' + that.input3 + '&id=' + that.input1 + '')
										.then((res) => {
											if(res.data == 0) {
												this.$message.error('注册失败');
											} else {
												console.log(res.data)
												console.log(that.input2)
												that.$message({
													message: '修改成功',
													type: 'success',
												});
												this.$router.push("/xiugai")
											}
										})

								}

							}
						}

					})
			}
		}
	}
</script>

<style scoped>
	.el-row{
		margin-left: 600px;
	}
	div {
		margin-top: 15px;
	}
</style>


